'use server'

function ActionList({ action }) {
    return (
        <Link
            href={"/dashboard/workflow/" + workflow.id}
        >
            <div className="flow flow-col gap-2 border border-solid border-black rounded-md p-2">
                <h2>{workflow.base_action.metadata[1].key + ": " + workflow.base_action.metadata[1].value}</h2>
                <p>{workflow.base_action.metadata[0].key + ": " + workflow.base_action.metadata[0].value}</p>
            </div>
        </Link>
    )
}

export default async function WorkflowPage({ params }) {
    const { id } = await params;
    const url = process.env.SERVER_URL || ""
    console.log(url + "/workflows?workflowId=" + id);
    const data = await fetch(url + "/workflows?workflowId=" + id,
        {
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            }
        }
    )
    const workflow = (await data.json())[0];
    console.log(workflow);

    return (
        <>
            <h1 className="text-5xl font-bold w-fit mt-4 mx-auto">Workflow</h1>
            <div className="flex flex-col gap-4 my-4 w-4/5 m-auto border rounded-xl p-4">
                <div>
                    <h2 className="text-xl font-bold">Metadata</h2>
                    <div className="flex flex-col gap-2 border border-solid border-black rounded-xl p-2">
                        {(workflow.base_action.metadata).map((data) => (
                            <div key={data.id} className="border border-solid border-black rounded-md p-2">
                                {data.key + ": " + data.value}
                            </div>
                        ))}
                    </div>
                </div>
                <div>
                    <h2 className="text-xl font-bold">Attributes</h2>
                    <div className="flex flex-col gap-2 border border-solid border-black rounded-xl p-2">
                        <div>{"Type: " + workflow.base_action.action_type}</div>
                        <div>{"Frozen: " + workflow.base_action.is_frozen}</div>
                    </div>
                </div>
            </div>
            <div className="w-4/5 m-auto p-4">
                <h2 className="text-xl font-bold">Actions</h2>
                <div className="flex flex-col gap-2 border border-solid border-black rounded-xl mx-[-16] p-4">
                    {(workflow.base_action.metadata).map((data) => (
                        <div key={data.id} className="border border-solid border-black rounded-md p-2">
                            {data.key + ": " + data.value}
                        </div>
                    ))}
                </div>
            </div>
        </>
    )
}